@charset "UTF-8";
.page {
  position: absolute;
  height: 100%;
  width: 100%;
  background-size: 100%;
  display: flex;
  overflow: hidden; }

.panel {
  width: 135px;
  height: 150px;
  position: relative;
  padding: 5px;
  top: 32.5%;
  margin: 0 auto;
  background: white; }
  .panel .qrcode {
    width: 125px;
    height: 125px;
    position: relative;
    display: block;
    background-size: 100% 100%;
    font-size: 8px;
    text-align: center;
    background-blend-mode: screen; }
    .panel .qrcode::after {
      position: absolute;
      bottom: -15px;
      left: 0;
      width: 100%;
      color: #D43A50;
      content: '更多详情请关注金沙洲万达广场'; }
    .panel .qrcode::before {
      content: ' ';
      height: 100px;
      width: 100px;
      position: absolute;
      top: -20px;
      left: 140px;
      background-size: 100px;
      background-repeat: no-repeat;
      background-image: url("img/page/P2-dialog.png"); }
  .panel .loveshare {
    position: relative;
    top: 40px;
    background-image: url("img/page/P2-btn.png");
    width: 120px;
    height: 100px;
    background-repeat: no-repeat;
    background-size: 120px; }

.disappear {
  display: none; }

@keyframes enter {
  0% {
    left: 100%; }
  100% {
    left: 0; } }

.enterPage {
  background-image: url("img/page/P1.jpg"); }

.acceptPage {
  background-image: url("img/page/bg.png"); }

.qrcodePage {
  background-image: url("img/page/P2.png"); }

.sendPage {
  background-image: url("img/page/bg.png"); }

.gamePage {
  background-image: url("img/page/P6-bg.png"); }

.messagePage {
  background-image: url("img/page/P8.png"); }

.ipadPage {
  background-image: url("img/page/ipad.jpg"); }

.card200Page {
  background-image: url("img/page/card200.jpg"); }

.hotwindPage {
  background-image: url("img/page/hotwind.jpg"); }

.miPowerPage {
  background-image: url("img/page/miPower.jpg"); }

.eggRollPage {
  background-image: url("img/page/eggRoll.jpg"); }

.selfilePage {
  background-image: url("img/page/selfile.jpg"); }

.dataLinePage {
  background-image: url("img/page/dataLine.jpg"); }

.usbFanPage {
  background-image: url("img/page/usbFan.jpg"); }

.beautyPage {
  background-image: url("img/page/beauty.jpg"); }

.playerCoinPage {
  background-image: url("img/page/playerCoin.jpg"); }

.endPage {
  background-image: url("img/page/end.png"); }

.qrcodePage,
.sendPage,
.gamePage,
.ipadPage,
.card200Page,
.hotwindPage,
.beautyPage,
.eggRollPage,
.endPage,
.playerCoinPage,
.usbFanPage,
.dataLinePage,
.selfilePage,
.miPowerPage {
  animation: enter 0.4s; }

.priceCode {
  width: 125px;
  height: 125px;
  position: absolute;
  background-size: 100% 100%;
  bottom: 0;
  background-blend-mode: screen;
  animation: up 10s; }

@keyframes up {
  0% {
    opacity: 0;
    bottom: -150px; }
  100% {
    opacity: 1;
    bottom: 0; } }
